import React, { useState } from "react";
import axios from "axios";
import Modal from './Modal'
import { url } from "../config";

const ColSearch = () => {
  const [colField, setColField] = useState("");
  const [colTables, setColTables] = useState([]);

  const hideModal = () => {
    document.getElementById('modalId').classList.add('-z-50')
    document.getElementById('modalId').classList.add('opacity-0')
    document.getElementById('modalId').classList.add('scale-50')
    document.getElementById('modalId').classList.remove('z-[99999]')
    document.getElementById('modalId').classList.remove('opacity-100')
    document.getElementById('modalId').classList.remove('scale-100')

  }
  const showModal = () => {

    document.getElementById('modalId').classList.add('z-[99999]')
    document.getElementById('modalId').classList.add('opacity-100')
    document.getElementById('modalId').classList.add('scale-100')
    document.getElementById('modalId').classList.remove('scale-50')
    document.getElementById('modalId').classList.remove('-z-50')
    document.getElementById('modalId').classList.remove('opacity-0')

}

  const SearchColumn = (e) => {
    e.preventDefault()
    if (colField == "") {
      alert("Search field cannot be empty");
    } else {
      axios
        .get(`${url.BASE_URL}`+"relation/search/" + colField)
        .then((res) => {
          setColTables(res.data.result);
          console.log(res.data.result)
        })
        .catch((err) => console.log(err));
        showModal()
    }
  };

  return (
    <div className="flex items-center gap-3">
      <input
        className="border w-[310px] py-1.5 px-3 rounded-md"
        type="text"
        onChange={(event) => {
          setColField(event.target.value);
        }}
        placeholder="Search a column to get its related tables"
      />
      <button
        className="bg-[#3AADAA] h-[37px] shadow-lg w-[37px] flex items-center justify-center rounded-full border-[#3AADAA]"
        onClick={SearchColumn}
        type="submit"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="white"
          height="22"
          viewBox="0 -960 960 960"
          width="22"
        >
          <path d="M796-121 533-384q-30 26-69.959 40.5T378-329q-108.162 0-183.081-75Q120-479 120-585t75-181q75-75 181.5-75t181 75Q632-691 632-584.85 632-542 618-502q-14 40-42 75l264 262-44 44ZM377-389q81.25 0 138.125-57.5T572-585q0-81-56.875-138.5T377-781q-82.083 0-139.542 57.5Q180-666 180-585t57.458 138.5Q294.917-389 377-389Z" />
        </svg>
      </button>
      <div>
      </div>
      <Modal modalId="modalId" searched={colField} items={colTables} func={hideModal} />
    </div>
  );
};

export default ColSearch;
