import React, { useState} from 'react'

import data from '../assets/dataType.png'
import relation from '../assets/relation.png'
import select from '../assets/select.png'

const ColList = (props) => {
  const [searchTerm, setSearchTerm] = useState('')
  const dropDownBox = (list,index) =>{
    if(document.getElementById('dropDownList' + index).classList.contains('hidden')){
    document.getElementById("dropDownList" + index).classList.add("flex")
    document.getElementById("dropDownList" + index).classList.remove("hidden")
    }else{
      document.getElementById("dropDownList" + index).classList.add("hidden")
      document.getElementById("dropDownList" + index).classList.remove("flex")
    }
  }

  return (
    <div className='rounded-md  border shadow-md'>
      <div className="p-2 w-full">
            <input
              className="border w-full py-1.5 px-3 rounded-md"
              type="text"
              onChange={(event) => {
                setSearchTerm(event.target.value);
              }}
              placeholder="Search"
            />
          </div>
          <div className='py-2 bg-[#F5F5F5] text-[#1A3454] font-semibold px-3 border-b border-t'>
            <span>Total Columns: <b>{props.table && Object.keys(props.data[props.table]).length}</b></span>
          </div>
          <div className="h-[0.5px] w-full bg-gray-300 "></div>
    <div className='h-full overflow-auto  text-ellipsis w-full scrollsettings overflow-x-hidden'>
     <ol className="list-none text-ellipsis overflow-hidden">
              {props.table && Object.keys(props.data[props.table])
                .filter((list) => {
                  if (searchTerm == "") {
                    return list;
                  } else if (
                    list.toLowerCase().includes(searchTerm.toLowerCase())
                  ) {
                    return list;
                  }
                })
                .map((list,index) => {
                  return (
                    <div className='border'>
                    <li onClick={(e)=>dropDownBox(list,index)} className="py-2 text-[#1A3454] flex font-medium justify-between items-center px-3 text-ellipsis whitespace-nowrap overflow-hidden cursor-pointer hover:border rounded-sm hover:shadow-md">
                    <li
                      key={list}
                    >
                      {list}
                      </li>
                      <div id={'downarrow'+index} className=" hover:bg-gray-300 ml-3 flex items-center justify-center rounded-full w-[15px] h-[15px]">
                      <svg xmlns="http://www.w3.org/2000/svg" height="17" viewBox="0 -960 960 960" width="17"><path d="M480-345 240-585l43-43 197 198 197-197 43 43-240 239Z"/></svg>
                      </div>
                    </li>
                    <ol id={'dropDownList' + index} className='px-3 hidden mb-2 flex-col'>
                      <li className='font-light flex gap-1 items-center'>
                        <div>
                        <img src={data} width="15" height="15" alt="" /></div>
                        <span>
                        <span className='font-normal'>
                        Type:</span> {props.data[props.table][list].type}</span></li>
                      <li className='font-light flex gap-1'><span className='font-normal flex items-center gap-1'>
                      <svg viewBox="0 0 24 24" height="15" width="15" fill="none" xmlns="http://www.w3.org/2000/svg"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"> <path d="M15.9101 12.3699H20.6001C20.5201 17.0399 19.6001 17.8099 16.7301 19.5099C16.4001 19.7099 16.2901 20.1299 16.4901 20.4699C16.6901 20.7999 17.1101 20.91 17.4501 20.71C20.8301 18.71 22.0101 17.4899 22.0101 11.6699V6.27997C22.0101 4.56997 20.6201 3.18994 18.9201 3.18994H15.9201C14.1601 3.18994 12.8301 4.51997 12.8301 6.27997V9.27997C12.8201 11.04 14.1501 12.3699 15.9101 12.3699Z" fill="#FF9900"></path> <path opacity="0.4" d="M5.08982 12.3699H9.77982C9.69982 17.0399 8.77982 17.8099 5.90982 19.5099C5.57982 19.7099 5.46982 20.1299 5.66982 20.4699C5.86982 20.7999 6.28982 20.91 6.62982 20.71C10.0098 18.71 11.1898 17.4899 11.1898 11.6699V6.27997C11.1898 4.56997 9.79982 3.18994 8.09982 3.18994H5.09982C3.33982 3.18994 2.00982 4.51997 2.00982 6.27997V9.27997C1.99982 11.04 3.32982 12.3699 5.08982 12.3699Z" fill="#FF9900"></path> </g></svg>
                        String:</span> {props.data[props.table][list].string}</li>
                        {(props.data[props.table][list].type == 'one2one' || props.data[props.table][list].type == 'one2many' || props.data[props.table][list].type == 'many2one' || props.data[props.table][list].type == 'many2many')?
                         <li className='font-light flex items-center gap-1'>
                        <div>
                        <img src={relation} width="15" height="15" alt="" /></div>
                        <span className='font-normal'>
                          Relation:</span> {props.data[props.table][list].relation}</li>: undefined
                        }
                        {(props.data[props.table][list].type == 'selection')?
                         <li className='font-light'>
                          <div className='flex gap-1 items-center'>
                          <img src={select} width="16" height="16" alt="" />
                          <span className='font-normal'>
                          Selection:</span>
                          </div>
                          <ol className='list-disc marker:text-[#FFAD33] ml-5'>
                          {
                            Object.keys(props.data[props.table][list].selection).map((li)=>{
                            return(
                              <li className='font-light'>{props.data[props.table][list].selection[li]}</li>
                            )
                            })
                          }
                          </ol>
                          </li>: undefined
                        }
                    </ol>
                    </div>
                  );
                })}
            </ol>
    </div>
    </div>
  )
}

export default ColList