import React, { useState, useEffect } from "react";
import axios from "axios";
import ReactFlow, { Controls, MiniMap, MarkerType } from 'reactflow';
import 'reactflow/dist/style.css';
import dagre from 'dagre';
import ColList from "./components/ColList";
import ColSearch from "./components/ColSearch";
import Loader from "./components/loader/Loader";
import { url } from "./config";
import Swal from "sweetalert2";

var nodes = [];

function App() {
  const [searchTerm, setSearchTerm] = useState("");
  const [data, setData] = useState([]);
  const [selectedTable, setTable] = useState();
  const [condition, setCondition] = useState(false)
  const [newClicked, setNewClicked] = useState()
  const [tableClicked, setTableClicked] = useState("")
  const [captureElementClick, setCaptureElementClick] = useState(true);
  const [lastList, setLastList] = useState('')
  const [isLoading, setIsLoading] = useState(true);
  const [rows, setRows] = useState()
  const [totalRecords, setTotalRecords] = useState()

  var xx = 110;
  var yy = 10;

  const subMenu = (list, index) => {
    setTableClicked(list)
    setLastList(list)
    if (list == lastList) {
      document.getElementById('submenu').classList.remove('flex')
      document.getElementById('submenu').classList.add('hidden')

      setLastList('')
    } else {
      if (document.getElementById('submenu').classList.contains('flex')) {
        document.getElementById('submenu').classList.remove('flex')
        document.getElementById('submenu').classList.add('hidden')
      }
      document.getElementById('submenu').classList.remove('hidden')
      document.getElementById('submenu').classList.add('flex')
    }
  }

  const proOptions = { hideAttribution: true };

  const dagreGraph = new dagre.graphlib.Graph();
  dagreGraph.setDefaultEdgeLabel(() => ({}));

  const nodeWidth = 250;
  const nodeHeight = 80;

  const getLayoutedElements = (nodes, edges, direction = 'TB') => {
    const isHorizontal = direction === 'LR';
    dagreGraph.setGraph({ rankdir: direction });

    nodes.forEach((node) => {
      dagreGraph.setNode(node.id, { width: nodeWidth, height: nodeHeight });
    });

    edges.forEach((edge) => {
      dagreGraph.setEdge(edge.source, edge.target);
    });

    dagre.layout(dagreGraph);

    nodes.forEach((node) => {
      const nodeWithPosition = dagreGraph.node(node.id);
      node.targetPosition = isHorizontal ? 'left' : 'top';
      node.sourcePosition = isHorizontal ? 'right' : 'bottom';

      node.position = {
        x: nodeWithPosition.x + 300,
        y: nodeWithPosition.y + nodeHeight / 2,
      };

      return node;
    });
    nodes[0].position = { x: 0, y: nodes[0].position.y }
  };

  var edges = [];


  const allData = () => {
    console.log('alldata')
    nodes = []
    Object.keys(data).map((things) => {
      nodes.push({
        id: things,
        position: { x: xx, y: yy },
        style: { width: 'auto' },
        data: { label: things },
      })
      Object.keys(data[things]).map((column) => {
        if (data[things][column]["type"] == "many2one" || data[things][column]["type"] == "many2many") {

          edges.push({
            id: things + '+' + column, source: things, target: data[things][column]["relation"].replace(/\./g, '_'), label: column,
            markerEnd: {
              type: MarkerType.Arrow,
              color: '#FF0072',
            }, labelBgPadding: [8, 4],
            labelBgBorderRadius: 4,
            labelBgStyle: { fill: '#FFCC00', color: '#fff', fillOpacity: 0.7 },

          })

        }

      })
    })
    getLayoutedElements(nodes, edges, "LR")
  }

  function selectedTablenodes(table) {
    console.log('selectedtablenode')
    nodes = [{
      id: selectedTable,
      position: { x: xx, y: yy },
      data: { label: selectedTable },
      style: { width: 'auto' },
      selected: true,
      draggable: true,
      selectable: true,
    }]
    edges = []

    var scopeTables = []
    scopeTables.push(selectedTable)
    if(!data[selectedTable]){
      Swal.fire({
        icon: 'warning',
        title: 'Cannot find relations for this table',
        confirmButtonText: "Refresh",
        confirmButtonColor: '#3AADAA',
        text: 'This table is an internal Odoo table or its relations does not exists',
      }).then((result)=>{
        if (result.isConfirmed) {
          window.location.reload()
        }
      })
    }else
    Object.keys(data[selectedTable]).map((column) => {
      if (data[selectedTable][column]["type"] == "many2one" || data[selectedTable][column]["type"] == "many2many") {
        if (!scopeTables.includes(data[selectedTable][column]["relation"].replace(/\./g, '_'))) {
          scopeTables.push(data[selectedTable][column]["relation"].replace(/\./g, '_'))
          nodes.push({
            id: data[selectedTable][column]["relation"].replace(/\./g, '_'),
            position: { x: xx, y: yy },
            style: { width: 'auto' },
            data: { label: data[selectedTable][column]["relation"].replace(/\./g, '_') },
          })
        }
        edges.push({
          id: selectedTable + '+' + column, source: selectedTable, target: data[selectedTable][column]["relation"].replace(/\./g, '_'), label: column, labelBgPadding: [8, 4],
          labelBgBorderRadius: 4,
          markerEnd: {
            type: MarkerType.ArrowClosed,
            color: '#00000',
            width: 25,
            height: 25,
          },
          style: {
            strokeWidth: 1,
          },
          labelBgStyle: { fill: '#FFCC00', color: '#fff', fillOpacity: 0.7 },
        })
      }
    })
    getLayoutedElements(nodes, edges, "LR")
  }

  const onNodeClick1 = (event, node) => {
    console.log(node)
    if (!node.id.includes('1')) {
      setNewClicked(node.data.label)
    } else {
      setTable(node.data.label)
      setNewClicked(false)
    }
  }


  function progressiveNodes(selectedTable) {
    console.log('progressive nodes')
    if (!selectedTable) {
      return null
    }
    selectedTable = selectedTable.replace(/\./g, '_')

    var scopeTables = [selectedTable]
    var xNew = nodes.at(-1).position.x + 300
    var yNew = nodes.at(-1).position.y
    Object.keys(data[selectedTable]).map((column, index) => {
      if (data[selectedTable][column]["type"] == "many2one" || data[selectedTable][column]["type"] == "many2many") {
        if (!scopeTables.includes(data[selectedTable][column]["relation"].replace(/\./g, '_'))) {
          scopeTables.push(data[selectedTable][column]["relation"].replace(/\./g, '_'))
          nodes.push({
            id: "1" + data[selectedTable][column]["relation"].replace(/\./g, '_'),
            position: { x: xNew, y: yNew },
            style: { width: 'auto' },
            data: { label: data[selectedTable][column]["relation"].replace(/\./g, '_') },
          })
        }
        edges.push({
          id: selectedTable + '+' + column, source: selectedTable, target: "1" + data[selectedTable][column]["relation"].replace(/\./g, '_'), label: column, labelBgPadding: [8, 4],
          labelBgBorderRadius: 4,
          labelBgStyle: { fill: '#FFCC00', color: '#fff', fillOpacity: 0.7 },
        })
      }
    })
    getLayoutedElements(nodes, edges, "LR")
  }

  console.log(selectedTable)

  useEffect(() => {
    axios
      .get(`${url.BASE_URL}` + "relationfinder")
      .then((res) => {
        setData(res.data.tables);
        setRows(res.data.rows);
        setIsLoading(false)
      })
      .catch((err) => {
        console.log(err)
        setIsLoading(false)
      }
      );
  }, []);

  useEffect(() => {
    if (rows) {
      setTotalRecords(Math.abs(rows[selectedTable]) > 999 ? Math.sign(rows[selectedTable]) * ((Math.abs(rows[selectedTable]) / 1000).toFixed(1)) + 'k' : Math.sign(rows[selectedTable]) * Math.abs(rows[selectedTable]))
    }
  }, [selectedTable])


  return (
    <div className="App relative">
      {isLoading ? <Loader /> : undefined}
      <div className="p-5 h-[100vh] gap-5 flex">
        <div className="relative rounded-md border max-w-[370px] shadow-md">
          <div className="p-2 bg-[#f5f5f5] w-full">
            <input
              className="border w-full py-1.5 px-3 rounded-md"
              type="text"
              onChange={(event) => {
                setSearchTerm(event.target.value);
              }}
              placeholder="Search"
            />
          </div>
          <div className="h-[0.5px] w-full bg-gray-300 "></div>
          <div className="h-[88vh] overflow-hidden">
            <div className="h-full overflow-auto scrollsettings overflow-x-hidden">
              <div className="text-ellipsis">
                <ol className="list-none overflow-hidden">
                  {Object.keys(data)
                    .filter((list) => {
                      if (searchTerm == "") {
                        return list;
                      } else if (
                        list.toLowerCase().includes(searchTerm.toLowerCase())
                      ) {
                        return list;
                      }
                    })
                    .map((list, index) => {
                      return (
                        <li className=" flex justify-between h-[42px] items-center border-b-[1px]  text-ellipsis whitespace-nowrap overflow-hidden cursor-pointer hover:border rounded-sm hover:shadow-md">
                          <li
                            key={index}
                            className="text-[#1A3454] truncate font-medium px-3 w-full"
                            onClick={(event) => {
                              setTable(list);
                              setNewClicked(false)
                              setCondition(false)
                            }}
                          >
                            {list}
                          </li>
                          <div onClick={(e) => { subMenu(list, index) }} id={"arrow" + index} className="hover:bg-gray-300 hover:border-gray-300 hover:text-white border-l flex items-center h-full">
                            <svg xmlns="http://www.w3.org/2000/svg" height="15" viewBox="0 -960 960 960" width="15"><path d="m375-240-43-43 198-198-198-198 43-43 241 241-241 241Z" /></svg>
                          </div>
                        </li>
                      );
                    })}
                </ol>
              </div>
            </div>
          </div>
          <div id="submenu" className="min-w-[300px] z-[100] hidden left-[101%] top-[7%] absolute bg-white overflow-hidden max-h-[88.5vh]">
            <ColList data={data} table={tableClicked} />
          </div>
        </div>
        <div className="flex w-full gap-3 flex-col">
          <div className="w-full flex bg-[#f5f5f5] gap-3 justify-between items-center rounded-md border px-3 h-[60px] shadow-md">
            <div className="flex gap-5 items-center justify-center">
              <ColSearch />
              {totalRecords ? <div className=" flex flex-col px-3 items-start justify-center text-sm rounded-md font-medium text-[#1A3454] w-fit h-[45px] border">
              <span><b>{selectedTable}</b></span>
              <span>Total Records:<b>{totalRecords}</b></span>
              </div>
            :<div className=" flex flex-col justify-center text-sm px-3 items-start rounded-md font-medium text-[#1A3454] w-fit h-[45px] border">
              <span><b>{selectedTable}</b></span>
              {selectedTable?<span>No records in this table</span>:<span>Select a table to get records</span>}
            </div>}
            </div>
              <div className="flex gap-5">
                <div onClick={() => { window.location.href = `${url.BASE_URL}` + 'query-dashboard' }}
                  className="border-[#3AADAA] border cursor-pointer text-sm  shadow-md text-white rounded-md flex h-[38px] items-center w-[120px] ease-in-out duration-300 bg-[#3AADAA] px-3 pl-4 justify-center">
                  SQL&nbsp;Editor&nbsp;
                  <svg xmlns="http://www.w3.org/2000/svg" height="20" fill="white" viewBox="0 -960 960 960" width="20"><path d="m560-240-56-58 142-142H160v-80h486L504-662l56-58 240 240-240 240Z" /></svg>
                </div>
              </div>
            </div>

            <div className="w-full rounded-md h-full scrollsettings overflow-auto shadow-md border p-3">
              {selectedTable && selectedTablenodes(selectedTable)}
              {newClicked && progressiveNodes(newClicked)}
              <ReactFlow nodes={nodes}
                edges={edges}
                nodesDraggable
                proOptions={proOptions}
                onNodeClick={captureElementClick ? onNodeClick1 : undefined}
                fitView>
                <MiniMap />
                <Controls />
              </ReactFlow>
            </div>
          </div>
        </div>
      </div>
      );
}

      export default App;
