import React from "react";
import copy from "copy-to-clipboard";

const Modal = (props) => {
  const copyToClipboard = (e) => {
    const copytext = e.target.innerText;
    copy(copytext);
    document.getElementById("snackbar").classList.add('left-5')
    document.getElementById("snackbar").classList.remove("left-[-300px]");
    setTimeout(closeSnackBar, 3000);
  };

  const closeSnackBar = () => {
    document.getElementById("snackbar").classList.add('left-[-300px]')
    document.getElementById("snackbar").classList.remove("left-5");
  };

  return (
    <div
      id={props.modalId}
      className="fixed flex -z-50 opacity-0 justify-center duration-500 bg-black ease-in-out scale-50 items-center inset-0 backdrop-blur-sm bg-opacity-30 "
    >
      <div className="flex flex-col w-full  sm:w-[750px] text-gray-800 bg-white rounded-lg gap-4 p-6">
        <div className="flex flex-col h-full max-h-[600px] relative gap-4">
          <div className="flex items-center justify-between">
            <h2 className="text-[#1a3454] drop-shadow-sm flex font-semibold text-2xl">
              ALl tables related to&nbsp;
              <h2 className="font-bold">{props.searched}</h2>
            </h2>
            <div
              onClick={props.func}
              className=" group mr-1 cursor-pointer border-white rounded-sm "
            >
              <svg
                className="group-hover:rotate-90 group-hover:scale-125 duration-200 ease-linear"
                xmlns="http://www.w3.org/2000/svg"
                fill="black"
                height="24"
                viewBox="0 96 960 960"
                width="24"
              >
                <path d="m249 849-42-42 231-231-231-231 42-42 231 231 231-231 42 42-231 231 231 231-42 42-231-231-231 231Z" />
              </svg>
            </div>
          </div>
          <div className="flex items-center w-full gap-3 px-3 p-2 rounded-sm bg-[#EFF6FF] border-[#EFF6FF]">
          <svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20" fill="#60A5FA" class=""><path fill-rule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a.75.75 0 000 1.5h.253a.25.25 0 01.244.304l-.459 2.066A1.75 1.75 0 0010.747 15H11a.75.75 0 000-1.5h-.253a.25.25 0 01-.244-.304l.459-2.066A1.75 1.75 0 009.253 9H9z" clip-rule="evenodd"></path></svg>
          <h5 className="text-[#1D4ED8]">You can click on a table name to copy it</h5>
          </div>
          <div className="flex gap-3 flex-wrap overflow-auto scrollsettings overflow-x-hidden">
            {props.items.map((list) => {
              return (
                <div className="border relative rounded-md p-1.5 px-3">
                  <h5
                    className="cursor-pointer hover:text-blue-500"
                    onClick={(e) => copyToClipboard(e)}
                  >
                    {list}
                  </h5>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      <div
        id="snackbar"
        className="absolute duration-300 flex ease-in items-center gap-5 bottom-5 left-[-300px] w-[250px] justify-between px-3 p-2.5 border-[#F0FDF4] bg-[#F0FDF4] rounded-sm"
      >
        <div className="flex items-center gap-3">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="#4ADE80"
            height="20"
            viewBox="0 -960 960 960"
            width="20"
          >
            <path d="m421-298 283-283-46-45-237 237-120-120-45 45 165 166Zm59 218q-82 0-155-31.5t-127.5-86Q143-252 111.5-325T80-480q0-83 31.5-156t86-127Q252-817 325-848.5T480-880q83 0 156 31.5T763-763q54 54 85.5 127T880-480q0 82-31.5 155T763-197.5q-54 54.5-127 86T480-80Z" />
          </svg>
          <h4 className="text-[#166534] font-semibold">Copied!</h4>
        </div>
        <div
          onClick={closeSnackBar}
          className=" group mr-1 cursor-pointer border-white rounded-sm "
        >
          <svg
            className="group-hover:rotate-90 group-hover:scale-125 duration-200 ease-linear"
            xmlns="http://www.w3.org/2000/svg"
            fill="#22C55E"
            height="20"
            viewBox="0 96 960 960"
            width="20"
          >
            <path d="m249 849-42-42 231-231-231-231 42-42 231 231 231-231 42 42-231 231 231 231-42 42-231-231-231 231Z" />
          </svg>
        </div>
      </div>
    </div>
  );
};

export default Modal;
